.profile {
  padding: 40px 30px 0;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 940px;
  box-sizing: border-box;
  position: relative;
}

@media screen and (max-width: 740px) {
  .profile {
    flex-wrap: wrap;
    padding: 42px 19px 0;
    flex-direction: column;
  }
}