.cards {
  list-style: none;
  margin: 0;
  padding: 50px 30px 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(242px,282px));
  row-gap: 20px;
  column-gap: 17px;
  justify-content: center;
}

@media screen and (max-width: 740px) {
  .cards {
    padding: 37px 19px 0;
  }
}
