.header {
  padding: 45px 30px 0;
  box-sizing: border-box;
  width: 100%;
  max-width: 940px;
  flex-shrink: 0;
  display: flex;
  flex-wrap: wrap;
}

.header::after {
  content: '';
  margin-top: 40px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: block;
  width: 100%;
}

@media screen and (max-width: 740px) {
  .header::after {
    margin-top: 31px;
  }
}

@media screen and (max-width: 740px) {
  .header {
    padding: 28px 19px 0;
  }
}

@media screen and (max-width: 440px) {
  .header {
    padding: 28px 0 0;
  }
}