.profile__avatar {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background: url(../../../images/profile-avatar.png) center/cover no-repeat;
  min-width: 120px;
  min-height: 120px;
  margin-right: 30px;
  position: relative;
  border-radius: 60px;
}

.profile__avatar:focus {
  outline: none;
}

.profile__avatar:before {
  opacity: 0;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url(../../../images/Avatar_edit.svg) center/22% 22% no-repeat;
  transition: .5s;
}

.profile__avatar:hover::before {
  cursor: pointer;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.8);
}

@media screen and (max-width: 740px) {
  .profile__avatar {
    margin: 0 auto 26px;
  }
}